@import "/src/styles/mixins";

.field {
  flex-shrink: 0;
}

.label {
  @extend %flexCenter;
  width: 1.25rem;
  height: 1.25rem;
  border: 1px solid $lightGrey;
  border-radius: 0.375rem;
  cursor: pointer;
  &:hover {
    border-color: #000;
  }
  &.disabled {
    background-color: $grey;
    border-color: $grey;
  }
  svg {
    width: 80%;
  }
}

.input {
  position: absolute;
  left: -9999px;
  top: -9999px;
  opacity: 0;
  visibility: hidden;
  &:checked {
    ~ .label {
      background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
      border-color: $red;
    }
  }
}