@import '/src/styles/mixins';

.item {
  @extend %flexCenter;
  min-height: 2.25rem;
  padding: 0 0.75rem;
  font-family: $fontLight;
  border-radius: 3.125rem;
  background-color: #EEF0F2;;
  &:hover {
    background: $grey;
  }

  &.active,
  &:active {
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    color: #fff;
  }
}