@import "root.scss";
@import "fonts.scss";
@import "animation.scss";

html {
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}


body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-weight: unset;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: $fontMedium;
}

ul[class],
ol[class] {
  list-style: none;
  padding: 0;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}


input,
button,
textarea,
select {
  font: inherit;
}

input {
  appearance: none;
  transition: border-color .2s;
  outline: none;
}

button {
  appearance: none;
  padding: 0;
  background: none;
  cursor: pointer;
  border: none;
}

a,
button,
label {
  transition: background-color .2s, background .2s, border-color .2s, box-shadow .2s;
}



body {
  background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
}

@media (max-width: $laptop) {
  html {
    font-size: 13px;
  }
}

@media (max-width: $tablet) {
  html {
    font-size: 16px;
  }
}