@import "/src/styles/root";

.item {
  width: 100%;
  height: 3.5rem;
  border: 1px solid transparent;;
  border-radius: 6px;
  color: #fff;
  &.fill {
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    box-shadow: 0 0 1.5rem rgba(234, 0, 41, 0.33);
    &:hover {
      background: $red;
    }
  }
  &.empty {
    background-color: transparent;
    border-color: #fff;
    &:hover {
      color: #000;
      background-color: #fff;
    }
  }
  &.disabled {
    background: $grey;
    box-shadow: none;
  }
  &.promo {
    width: 12.375rem;
  }
}