@import '/src/styles/root';

.item {
  position: absolute;
  overflow: auto;
  top: 7.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 34.5rem;
  max-height: 100%;
  padding: 1.6875rem 1.6875rem 2rem 2rem;
  border-radius: 30px;
  background-color: #fff;
}

.close {
  position: absolute;
  right: 1.6875rem;
  top: 1.6875rem;
}

.text {
  margin-top: 1rem;
  font-family: $fontLight;
  color: #808080;
}

.calc {
  margin-top: 1.5rem;
}
.decrease {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 1.75rem;

  &Text {
    flex-shrink: 0;
    color: #000;
  }

  &Tags {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding-right: 3.5rem;

    & > * {
      margin-right: 1rem;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}


.payment {
  display: flex;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid #DFE3E6;
  font-size: 0.875rem;
  cursor: pointer;

  &Value {
    margin-left: 0.75rem;
    color: #000;
  }

  &Year {
    margin-left: 4px;
    color: #808080;
  }
}

.footer {
  margin-top: 2.5rem;
}

@media (max-width: $laptop) {
  .item {
    top: 3.5rem;
  }
}

@media (max-width: $tablet) {
  .item {
    width: 453px;
  }
  .title {
    font-size: 28px;
  }
}

@media (max-width: $mobile) {
  .item {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    width: 100%;
    height: 100%;
    top: 0;
    border-radius: 0;
  }
  .title {
    font-size: 18px;
  }
  .text {
    font-size: 12px;
  }
  .decrease {
    flex-wrap: wrap;
    &Tags {
      justify-content: flex-start;
      margin-top: 24px;
    }
  }
  .footer {
    width: 100%;
  }

}