@import "/src/styles/root";

.field {
  &Error {
    .item {
      border-color: $red;
      &::placeholder {
        color: $red;
      }
      &:hover {
        border-color: $red;
      }
    }
    .error {
      opacity: 1;
    }
  }
}

.item {
  width: 100%;
  height: 2.5rem;
  margin-top: 0.5rem;
  padding: 0 0.625rem;
  border: 1px solid $grey;
  border-radius: 3px;
  font-family: $fontLight;
  .disabled {
    pointer-events: none;
    border-color: #808080;
  }
  &:hover {
    border-color: #000;
  }
  &:focus,
  &:focus-visible {
    border-color: $grey;
  }
}

.label {
  display: block;
}

.error {
  opacity: 0;
  display: block;
  margin-top: 4px;
  font-size: 0.625rem;
  line-height: 1;
  color: $red;
  transition: opacity .2s;
}
